.header {
  display: flex;
  justify-content: space-between;
  border: 1px solid black;
}

.logo {
  width: 200px;
}

.nav-items > ul {
  display: flex;
  font-size: 24px;
  list-style-type: none;
}

.nav-items > ul > li {
  padding: 10px;
  margin: 10px;
}

.search {
  padding: 10px;
}

.res-container {
  display: flex;
  flex-wrap: wrap;
}
.res-card {
  margin: 10px;
  padding: 2px;
  width: 200px;
}
.res-card:hover {
  cursor: pointer;
  border: 1px solid black;
}

.res-logo {
  width: 100%;
}

.login {
  padding: 0 20px;
  cursor: pointer;
}

.shimmer-container {
  display: flex;
}

.shimmer-card {
  width: 200px;
  height: 400px;
  background-color: grey;
  margin: 20px;
}